import axios from "axios"

import state from "./state.js"

const actions = {
    addGood({commit}){
        axios({
                method: 'get',
                url: '/api/v1/shop/myshop_car/',
            }).then((res)=>{
                console.log(res)
                if(res.data.code == 0){
                    commit('addGood','')
                }else if(res.data.code == 1){
                    console.log(res.data)
                    commit('addGood',res.data.data)
                    
                }
            })
        },
    }
export default actions


