<template>
    <div class="artical-box">
        
        <artical-box-item :hotartical = "hotartical" v-for="hotartical in hotarticals" :key="hotartical.id"></artical-box-item>
    </div>
</template>

<script>
import axios from 'axios'
import ArticalBoxItem from "./artical-boxItem"
export default {
    name:"artical-box",
    components:{
        ArticalBoxItem
    },
    data(){
      return {
          hotarticals:[]
      }
    },
    methods:{
     gethotartical(){
         let that = this
         axios.post('/api/v1/index/newartical',{
             page:1
         }).then((res)=>{
             that.hotarticals = res.data.data
             console.log(that.hotarticals)
         })
     }  
  },
  created(){
      this. gethotartical()
  }
}        
</script>

<style lang="scss" scoped>
    
</style>




