<template>
    <div class="lots-boxdetail">
        <!-- <router-view></router-view> -->
        <div class="head-height">
            <lots-box-detail-head></lots-box-detail-head>
       </div>
       <div class="lots-articaldetail">
            <div class="lots-articaldetailtop">
                <h1>{{data.title}}</h1>
                <h2>
                    <span class="spanone">
                        <i>
                            <b class="fa fa-commenting-o"></b>
                            <strong>{{data.comment_num}}</strong>
                        </i>
                        <i class="itwo">
                            <b class="fa fa-eye"></b>
                            <strong>{{data.view_num}}</strong>
                        </i>
                    </span>
                    <span class="spantwo">
                        <b>{{data.time}}</b>
                        
                    </span>
                </h2>
            </div>
            <p></p>
            <div class="lots-articaldetailcenter">
                
                <span v-for=" (im,i) in img" :key="i"><img :src="im"></span>
                <div class="font">
                    {{data.news}}
                </div>
            </div>
       </div> 
    </div>
</template>

<script>
import LotsBoxDetailHead from '../lots-vue/lots-boxdetailhead'
import axios from "axios"
export default {
    name:"news-boxdetail",
     components:{
         LotsBoxDetailHead
     },
    data(){
        return{
            data:'',
            img:[],
            nid:'',
        }
    },
    mounted(){
        axios.post("/api/v1/index/news_detail/",{
            nid:this.$route.query.nid
            }).then((res)=>{
                console.log(res.data.news_data)
               this.data = res.data.news_data
               this.img = res.data.news_data.img
               this.nid = res.data.news_data.nid
           
        })
    }
}
</script>

<style lang="scss" scoped>
.lots-boxdetail{
    width:100%;
    height:100%;
    position: fixed;
    top:0;
    z-index:1000;
    background:#fff;
    .head-height{
        width:100%;
        height:44px;
    }
}
</style>




